@import '../../styles/vars';

@keyframes movingCranes {
  from {
    transform: rotateY(0deg);
  }

  to {
    transform: rotateY(40deg);
  }
}

.root {
  font-weight: 900;
  padding: 0.75rem;
  text-transform: uppercase;
  background: none;
  font-size: 2.25rem;
}

