.textWrapper {
  background: linear-gradient(180deg, rgba(63, 0, 216, 0.85) -6%, rgba(0, 255, 184, 0.95) 39%, rgba(255, 225, 5, 0.788235294117647) 96%);
  margin-bottom: 0.75rem;
}

